
@import url('https://fonts.googleapis.com/css2?family=Caudex&family=Petit+Formal+Script&family=Poppins:wght@300;400;500;600&display=swap');
*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: rgb(238, 8, 8);
    --color-darkSlate: #2F4F4F;
    --color-green: rgb(0, 104, 56);
    --color-primary-variant: rgb(190, 30, 45);
    --color-white: #fff;
    --color-light: rgba(255,255,255,0.6);
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
    --transition: all 0.5s ease;
}
html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: 'Poppins', sans-serif;
    /* background: var(--color-bg); */
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(); */
    /* position: relative; */
}

/* .container{
    width: var(--container-width-lg);
    margin:  0 auto;
} */

h1, h2, h3, h4, h5 {
    font-weight: 450;
}

h1{
    font-size: 2.5rem;
}

section{
    margin-top: 8rem;
    /* height: 100vh; */
}

section > h2, section > h5{
    text-align: center;
    color: var(--color-light);
}

section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;

}

text-light{
    color: var(--color-light);
}

a{
    color: var(--color-white);
    transition: var(--transition);
    cursor: pointer;
    
}

a:hover{
    color: var(--color-primary-variant);
    text-decoration: none;
}
.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/* Media Queries (MEDIUM DEVICES) */
@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 6rem;
    }
    
}

/* Media Queries (SMALL DEVICES) */
@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-sm);
    }
    section > h2{
        margin-bottom: 2rem;
    }
    .hovertext:before{
        top: 60%;
    }
    
}

/* hovertext */

.hovertext {
    position: relative;
    border-bottom: 1px dotted black;
  }
  
  .hovertext:before {
    content: attr(data-hover);
    visibility: hidden;
    opacity: 0;
    width: 12rem;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    font-size: x-small;
    padding: 5px 0;
    transition: opacity 1s ease-in-out;
  
    position: absolute;
    z-index: 1;
    text-align: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    left: 50%;
    top: 100%;
    transform: translate(-50%);
  }
  
  .hovertext:hover:before {
    opacity: 1;
    visibility: visible;
  }
